import React, { Component } from 'react';
import { Typography, ButtonBase } from '@material-ui/core/'
import styled, { keyframes } from 'styled-components'
import PropTypes from 'prop-types';

const hoveringRotateAnimation = keyframes`
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(360deg);
  }
`
const ButtonBaseRotation = styled(ButtonBase)`
  &:hover {
    animation: ${hoveringRotateAnimation} 1s ease-in-out;
  };
`
const hoveringScanAnimation = keyframes`
0% {
  height:100%;
  width:2px;
  margin-left: 0;
  opacity:0;
}
10%{
  opacity:0.5;
}
49% {
  height:100%;
  width:2px;
  margin-left:100%;
  opacity:1
}
50%{
  height:2px;
  width:100%;
  top:0;
  margin-left: 0;
  opacity:0;
}
60%{
  opacity:0.5
}
100% {
  height:2px;
  width:100%;
  top:100%;
  opacity:1;
}
`
const ButtonBaseScan = styled.div`
  position:absolute; 
  left:0;
  top:0;
  background-color:${props => props.lineColor ? props.lineColor : '#2b2b2b'};
  height:${props => props.isPressed ? '2px': null};
  width:${props => props.isPressed ? '100%': null};
  top:${props => props.isPressed ? '100%': null};
  opacity:${props => props.isPressed ? '1': null};
  animation: ${props => props.isHovering ? hoveringScanAnimation : null} 1s ease;
  animation-fill-mode: forwards; 
`;
export default class AnimatedButton extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isHovering: false,
    };
  }

  handleMouseHover() {
    this.setState({ isHovering: !this.state.isHovering });
  }

  render() {
    return (
      <ButtonBaseRotation
        centerRipple
        onMouseEnter={() => this.handleMouseHover()}
        onMouseLeave={() => this.handleMouseHover()}
        onClick={() => this.props.onButtonPress()}
        style={{color: this.props.pressed ? 
          this.props.colorPressed ? this.props.colorPressed : '#2b2b2b'
          : this.props.color ? this.props.color : '#a1a1a1' }}
      >
          <ButtonBaseScan isHovering={this.state.isHovering} isPressed={this.props.pressed} lineColor={this.props.lineColor}/>
          <Typography component="span" variant="h1" color="inherit" style={{ fontSize: 18, fontWeight:400,color:this.props.fontColor }}>{this.props.buttonText}</Typography>
      </ButtonBaseRotation>
    )
  }
}

AnimatedButton.propTypes = {
  buttonText: PropTypes.string.isRequired,
  pressed: PropTypes.bool.isRequired
};
AnimatedButton.defaultProps = {
  buttonText: "Bot√≥n",
  pressed: false,
};