import React, { Component } from 'react';
import { Grid, Typography } from '@material-ui/core';
import styled, { keyframes } from 'styled-components'

import WorkOutlineOutlined from '@material-ui/icons/WorkOutlineOutlined';
import SchoolOutlined from '@material-ui/icons/SchoolOutlined';
import BuildOutlined from '@material-ui/icons/BuildOutlined';

import SectionNav from './SectionNav'

const ExperienciaDiv = styled.div`
position:absolute;
bottom:0;
left:${props => props.isMobile ? 0 : props.sectionFocus >= 1 ? '5%' : '85%'};
width: ${props => props.isMobile ? '100%' : props.sectionFocus == 1 ? '85%' : '5%'};
height: ${props => !props.isMobile ? '100%' : props.sectionFocus == 1 ? '85%' : '4em'};
top: ${props => !props.isMobile ? 0 : props.sectionFocus < 1 ? '85%' : '5%'};
transition: all 2s;
overflow: hidden;
background: #1C4854;
background: ${props => props.sectionFocus == 1 ? '-webkit-linear-gradient(to bottom, #1C4854 1%, #fff 30%)' : props.isMobile ? '#1C4854' : '-webkit-linear-gradient(to bottom, , #fff)'};
background: ${props => props.sectionFocus == 1 ? 'linear-gradient(to bottom, #1C4854 1%, #fff 30%)' : props.isMobile ? '#1C4854' : 'linear-gradient(to bottom, #1C4854, #fff)'};
`
const CardIcon = styled.div`
  opacity:1;
  animation: ${ props => props.focused ?
    keyframes`
    0% {
      opacity:1;
      transform: rotateX(0deg) rotateY(0deg);
    }
    15% {
      opacity:0.4;
    }
    25% {
      opacity:1;
      transform: rotateX(360deg) rotateY(360deg);
    }
    100% {
      opacity:1;
      transform: rotateX(360deg) rotateY(360deg);
    }`
    : null
  } 4s ease-in-out infinite;
`
const cardSelectAnimation = (toX, toY, height) => keyframes`
  from {
    transform: rotateY(-45deg);
  }
  to {
    left:${toX}px;
    bottom:${toY}px;
    height:${height}px;
    transform: rotateY(360deg) scale(1);
  }
`
const CardDeck = styled.div`
  width:${props => props.isMobile ? props.screenWidth * 0.8 : props.screenWidth * 0.7}px;
  height:${props => props.isMobile ? props.screenHeight * 0.8 : props.screenWidth * 0.7 * 9 / 16}px;
  border-radius: 3%;
  background: ${
  props => props.selectedIndex == props.index ?
    (
      '#000000',
      '-webkit-linear-gradient(to bottom, rgba(0,0,0,1), rgba(11, 6, 10, 1), rgba(14, 13, 21, 1), rgba(12, 20, 28, 1), rgba(4, 26, 33, 1), rgba(4, 32, 37, 1), rgba(4, 37, 40, 1), rgba(7, 43, 42, 1))',
      'linear-gradient(to bottom, rgba(0,0,0,1), rgba(11, 6, 10, 1), rgba(14, 13, 21, 1), rgba(12, 20, 28, 1), rgba(4, 26, 33, 1), rgba(4, 32, 37, 1), rgba(4, 37, 40, 1), rgba(7, 43, 42, 1))'
    )
    : props.hoveredIndex == props.index ?
      (
        'rgba(0, 143, 122,0.5)'
      )
      : props.index > props.selectedIndex ? // mas adelante
        (
          '#072b2a',
          '-webkit-linear-gradient(to top, rgba(0, 143, 122,0.5), rgba(7,43,42,0.3));',
          'linear-gradient(to top, rgba(0, 143, 122,0.5), rgba(7,43,42,0.3) );'
        )
        :
        (
          'rgba(7,43,42,0.2);'
        )};
  position: absolute;
  bottom:${props => props.isMobile ?
    props.screenHeight * 0.05
    : ((props.screenHeight - props.screenWidth * 0.7 * 9 / 16) * 0.5)}px;
  left:${props => props.isMobile ? props.screenWidth * 0.1 : props.screenWidth * 0.15}px;
  z-index: ${
  props => props.index == props.selectedIndex ?
    10000
    : props.index > props.selectedIndex ?
      900 - props.index
      : 900 + props.index
  }
  animation:${
  props =>
    !props.display ?
      keyframes`
  0% {
    transform:translate(0);
  }
  100% {
    transform:translate(100%,0);
  }`
      : props.index == props.selectedIndex ?
        keyframes`
  0% {
    transform:translate(0);
  }
  50% {
    transform:rotateZ(-15deg) translate(-20%, -50%);
  }
  100% {
    transform:rotateZ(0) translate(0);
  }`
        : props.index > props.selectedIndex ?
          keyframes`
  0% {
    transform:translate(0);
  }
  50% {
    transform:rotateZ(5deg) translate(5%, 10%);
  }
  100% {
    transform: translate(35%,-10%) rotateZ(${5 + (props.selectedIndex - props.index) * 3}deg)
  }`
          : keyframes`
    0% {
      transform:translate(0);
    }
    50% {
      transform:rotateZ(-5deg) translate(-5%, 10%);
    }
    100% {
      transform: translate(-35%,10%) rotateZ(${5 + (props.selectedIndex - props.index) * 3}deg)
    }`
  } 1s ease-in-out,   
  ${ props => props.index < props.selectedIndex ?
    keyframes`
    0% {
      opacity:0.4;
    }
    50% {
      opacity: 3;
    }
    100% {
      opacity:0.4;
    }`
    : null
  } 5s ease-in-out infinite;
  animation-fill-mode: forwards, none;
  transform-origin:50% 100%;
  transition: all 1s ease-in-out;
`
export default class Experiencia extends Component {
  constructor(props) {
    super(props);

    this.timelineSize = this.props.screenWidth

    this.state = {
      hoverRotation: 0,
      cardIndexFocus:  6,
      cardIndexHovered: 6
    };
  }

  componentWillUnmount() {
    clearInterval(this.timerNext);
  };

  startHoverTimer() {
    clearInterval(this.timerHover);
    this.timerHover = setInterval(
      () => {
        this.setState({ hoverRotation: this.state.hoverRotation + 10 })
      }, 200)
  }

  stopHoverTimer() {
    clearInterval(this.timerHover);
  }

  onCardFocus(index) {
    this.setState({ cardIndexFocus: index })
  }

  onMouseEnter(index) {
    this.setState({ cardIndexHovered: index })
  }

  render() {
    const {labels} = this.props
    const list = labels.experiencia.list
    return (
      <ExperienciaDiv
      onClick={() => this.props.onClick()}
        sectionFocus={this.props.sectionFocus} isMobile={this.props.isMobile}>
        <SectionNav labels={labels} section={1} isMobile={this.props.isMobile} focus={this.props.sectionFocus == 1} onSelect={() => this.props.onClick()} />

        <div style={{ width: '100%', top: '4em', backgroundColor: 'red' }}>
          {
            list.map((card, index) => (
              <CardDeck
                key={index}
                display={this.props.sectionFocus == 1 ? 1 : 0}
                isMobile={this.props.isMobile}
                screenWidth={this.props.screenWidth}
                screenHeight={this.props.screenHeight}
                index={index}
                selectedIndex={this.state.cardIndexFocus}
                hoveredIndex={this.state.cardIndexHovered}
                onMouseEnter={() => this.onMouseEnter(index)}
                onMouseDown={() => this.onCardFocus(index)}>

                <Grid container direction='column' justify='space-between' alignItems='center'
                  style={{ width: '100%', height: '100%', padding: '5%', }}>
                  <Grid item container direction='row' justify='space-between' alignItems='center' style={{ flexWrap: 'nowrap' }}>
                    <CardIcon focused={this.state.cardIndexFocus == index}>
                      {
                        card.tipo == "w" ?
                          <WorkOutlineOutlined fontSize='large' style={{ color: '#fff' }}></WorkOutlineOutlined>
                          : card.tipo == "ed" ?
                            <SchoolOutlined fontSize='large' style={{ color: '#fff' }}></SchoolOutlined>
                            : <BuildOutlined fontSize='large' style={{ color: '#fff' }}></BuildOutlined>
                      }
                    </CardIcon>
                    <Typography variant="h2" style={{ fontSize: '1.7em', color: '#fff', fontWeight: 300, whiteSpace: 'normal', textAlign: 'left', paddingLeft: '1em' }}>{card.titulo}</Typography>
                  </Grid>
                  <div style={{ maxHeight: '50%', overflowY: 'auto' }}>
                    <Typography variant="subtitle1" style={{ fontSize: this.props.isMobile ? '1em' : '1.2em', color: '#fff', fontWeight: 300, whiteSpace: 'normal', textAlign: 'left' }}>{card.descripcion}</Typography>
                    {card.proyecto != "" ? <Typography variant="subtitle1" style={{ fontSize: '1.2em', color: '#fff', fontWeight: 300, whiteSpace: 'normal', textAlign: 'left' }}>{card.proyecto}</Typography> : null}
                  </div>
                  <div >
                    <Typography variant="body1" style={{ fontSize: '1em', color: '#fff', fontWeight: 200, whiteSpace: 'normal', textAlign: 'center' }}>{card.establecimiento} - {card.ciudad}</Typography>
                    <Typography variant="body2" style={{ fontSize: '1em', color: '#fff', fontWeight: 200, whiteSpace: 'normal', textAlign: 'center' }}>{card.fecha}</Typography>
                  </div>
                </Grid>

              </CardDeck>
            ))
          }
        </div>

      </ExperienciaDiv>
    );
  }
}
