import React, { Component } from 'react';
import { AppBar, Toolbar, Typography, Grid, ButtonBase } from '@material-ui/core/'
import styled, { keyframes } from 'styled-components'

import AnimatedButton from './AnimatedButton'

import CodeOutlined from '@material-ui/icons/CodeOutlined';
import ClassOutlined from '@material-ui/icons/ClassOutlined';
import SmsOutlined from '@material-ui/icons/SmsOutlined';

export default class SectionNav extends Component {
  constructor(props) {
    super(props);

    this.state = {
      botonIndex: 0
    };
  }

  componentDidMount() {
  }

  onButtonPress(index) {
    this.props.onButtonPress(index)
    if (this.state.botonIndex != index)
      this.setState({ botonIndex: index })
  }

  render() {
    const {labels} = this.props
    const iconFontSize = this.props.isMobile && !this.props.focus ? '1.5em' : '2em'
    return (
      <div
        style={{
          position: 'absolute', top: 0, left: 0, right: 0,
          height: this.props.isMobile && !this.props.focus ? '2em' : '4em',
          width:  '100%',
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'center',
          alignItems: 'center'
        }}>
          {
            this.props.section == 1 ?
              <ClassOutlined style={{ color: '#fff', fontSize:  iconFontSize, margin: '0 10px' }} />
              : this.props.section == 2 ?
                <CodeOutlined style={{ color: '#fff', fontSize: iconFontSize, margin: '0 10px' }} />
                : this.props.section == 3 ?
                  <SmsOutlined style={{ color: '#fff', fontSize: iconFontSize, margin: '0 10px' }} />
                  : null
          }
          {
            this.props.focus ?
              this.props.section == 1 ?
                < Typography variant="h1" style={{ fontSize: '2em', color:'#fff', fontWeight: 300 }}>{labels.experiencia.titulo}</Typography>
                : this.props.section == 2 ?
                  <Typography variant="h1" style={{ fontSize: '2em', color: '#fff', fontWeight: 300 }}>{labels.proyectos.titulo}</Typography>
                  : this.props.section == 3 ?
                    <Typography variant="h1" style={{ fontSize: '2em', color: '#fff', fontWeight: 300 }}>{labels.contacto.titulo}</Typography>
                    : null
              : null
          }
      </div >
    )
  }
}