const LandingLeftDiv = styled.div`
  position:absolute;
  bottom:${props => !props.isMobile ? 0 : props.focus ? '15%' : '95%'};
  right:${props => props.isMobile ? 0 : props.focus ? '15%' : '95%'};
  width: ${props => props.isMobile ? '100%' : '85%'};
  height: ${props => !props.isMobile ? '100%' : '85%'}; 
  transition: all 2s;
  overflow: hidden;
  background: #000000;
  background: -webkit-linear-gradient(to bottom, #000000, #0b060a, #0e0d15, #0c141c, #041a21, #042025, #042528, #072b2a);
  background: linear-gradient(to bottom, #000000, #0b060a, #0e0d15, #0c141c, #041a21, #042025, #042528, #072b2a);
 `
const ImgDiv = styled.div`
  position: absolute; 
  top: ${props => (props.screenHeight - (props.isMobile ? props.screenWidth * 0.7 : props.screenWidth * 0.85 * 0.7)) / 2}px;
  left: ${props => props.isMobile ? props.screenWidth * 0.15 : props.screenWidth * 0.85 * 0.15}px;
  width: ${props => props.isMobile ? props.screenWidth * 0.7 : props.screenWidth * 0.85 * 0.7}px; 
  height: ${props => props.isMobile ? props.screenWidth * 0.7 : props.screenWidth * 0.85 * 0.7}px;
  border-radius:50%;
  overflow: hidden;
  -webkit-box-shadow: 0px 0px 220px -27px rgba(0,0,0,0);
  -moz-box-shadow: 0px 0px 220px -27px rgba(0,0,0,0);
  box-shadow: 0px 0px 220px -27px rgba(0,0,0,0);
  animation:
  ${ keyframes`
    from {
      opacity:0;
      transform:rotateY(0deg);
    }
    to {
      opacity:1
      transform:rotateY(360deg);
    }
  `} 2s ease;
  animation-delay: 0s;
  animation-fill-mode: forwards; 
`
