    `
    : null
  } 2s ease;
  animation-fill-mode: forwards; 
`
const AnimCardImg = styled.img`{
  background - position: center;
  background - size: cover;
  position: absolute;
  transition: all 0.3s ease;
    &: hover {
    opacity: 0.4;
    transform: scale(1.3);
  }
} `

export default class LandingLeft extends Component {
  constructor(props) {
    super(props);

    this.state = {
      hoverOnImg: false,
      isEsp: true
    };
  }

  onImgHover() {
    this.setState({ hoverOnImg: true })
  }

  onImgHoverOut() {
    this.setState({ hoverOnImg: false })
  }

  onEspaniol = () => {
    const { isEsp } = this.state
    if (!isEsp)
      this.setState({ isEsp: true }, () => this.props.onEspaniol() )
  }

  onEnglish = () => {
    const { isEsp } = this.state
    if (isEsp)
        this.setState({ isEsp: false }, () => this.props.onEnglish() )
  }

  render() {
    const { isEsp } = this.state
    const {focus, isMobile, labels} = this.props
    return (
      <LandingLeftDiv
        onMouseEnter={() => this.props.onMouseEnter()}
        focus={focus} isMobile={isMobile}>
     {focus &&    <div style={{ position: 'absolute', top: isMobile ? null : 10, right: isMobile ? null : 10, bottom:isMobile ?10:null , left:isMobile ?'35%':null, display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', alignItems: 'center' }}>
          <div onClick={this.onEspaniol} style={{cursor:'pointer'}}>
            <Typography variant="h3" component="h3" style={{ fontSize: isMobile ? 15 : 22, color: '#fff', opacity: isEsp ? 1 : 0.5, fontWeight: 200 }}>Espa√±ol</Typography>
          </div>
          <Typography variant="h3" component="h3" style={{ fontSize: isMobile ? 15 : 22, color: '#fff', fontWeight: 200 }}>/</Typography>
          <div onClick={this.onEnglish} style={{cursor:'pointer'}}>
            <Typography variant="h3" component="h3" style={{ fontSize: isMobile ? 15 : 22, color: '#fff', opacity: isEsp ? 0.5 : 1, fontWeight: 200 }}>English</Typography>
          </div>
        </div>}
        <ImgDiv screenWidth={this.props.screenWidth} screenHeight={this.props.screenHeight} isMobile={isMobile} onMouseEnter={() => this.onImgHover()} onMouseLeave={() => this.onImgHoverOut()}>
          <AnimCardImg src={require('../assets/IMG-20170409-WA0023-round.jpg')} alt='Leandro Ercoli'
            style={{ maxHeight: '100%', maxWidth: '100%', marginLeft: 'auto', marginRight: 'auto', display: 'block' }} />
        </ImgDiv>
        <NombreLine hoverOnImg={this.state.hoverOnImg} />

        <NombreDivOuter>
          <NombreDivInner width={isMobile ? this.props.screenWidth : this.props.screenWidth * 0.7}>
            <Typography variant="h3" component="h3" style={{ fontSize: isMobile ? 36 : this.state.hoverOnImg ? 48 : 42, color: '#fff', fontWeight: 200, transition: 'font-size 0.5s' }}>LEANDRO ERCOLI</Typography>
          </NombreDivInner>
        </NombreDivOuter>
        <NombreDivOuterSubtitulo>
          <NombreDivInnerSubtitulo width={isMobile ? this.props.screenWidth : this.props.screenWidth * 0.7} hoverOnImg={this.state.hoverOnImg}>
            <Typography variant="h3" component="h3" style={{ fontSize: isMobile ? 26 : 32, color: '#fff', fontWeight: 200 }}>{labels.landingLeft.titulo}</Typography>
          </NombreDivInnerSubtitulo>
        </NombreDivOuterSubtitulo>
      </LandingLeftDiv>
    )

  }
}


LandingLeft.propTypes = {
  isMobile: PropTypes.bool.isRequired,
  focus: PropTypes.bool.isRequired,
  screenWidth: PropTypes.number.isRequired,
  screenHeight: PropTypes.number.isRequired
};
LandingLeft.defaultProps = {
};
