import React, { Component } from 'react';
import { Grid, Paper, Typography } from '@material-ui/core';
import styled, { keyframes } from 'styled-components'
import Proyectos from './Proyectos'
import Experiencia from './Experiencia'

const divAnimation = (fromy, toy) => keyframes`
  from {
    height:${fromy}%;
  }
  to {
    height:${toy}%;    
  }
`
const AnimatedBackgroundDiv = styled.div` 
  background-color: black;
  position:absolute; 
  bottom:0;
  left:0;
  right:0;
  width:100%;
  height:0px;
  z-index:1;
  animation: ${props => divAnimation(props.fromy, props.toy)} 1s ease-in-out;
  animation-fill-mode: forwards; 
`

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lastdivy: 0,
      divy: this.props.section === "experiencia" ? 75 : this.props.section === "proyectos" ? 50 : 25
    };
  }

  componentWillMount() {
    this.timeouts = null;
  }
  componentWillUnmount = () => {
    clearTimeout(this.timeouts)
  };

  componentDidUpdate(prevProps) {
    if (this.props.section !== prevProps.section) {
      this.setState({
        lastdivy: this.state.divy,
        divy: this.props.section === "experiencia" ? 75 : this.props.section === "proyectos" ? 50 : 25
      })
    }
  }

   render() {
    return (
      <Grid container item xs direction='column' justify='space-evenly' alignItems='center' style={{ height: '100%' }}>
        <div
          style={{ width: 1, height: "100%", backgroundColor: 'red', position: 'absolute', left: '50%', zIndex: 100 }}></div>
        <AnimatedBackgroundDiv toy={this.state.divy} fromy={this.state.lastdivy} />
        {
          this.props.section == "proyectos" ?
            <Proyectos />
            : this.props.section == "experiencia" ?
              <Experiencia />
              : null
        }
      </Grid>
    );
  }
}

export default Main;
