import React, { Component } from 'react';
import { AppBar, Toolbar, Typography, Grid, ButtonBase } from '@material-ui/core/'
import styled, { keyframes } from 'styled-components'

import AnimatedButton from './AnimatedButton'

import CodeOutlined from '@material-ui/icons/CodeOutlined';
import ClassOutlined from '@material-ui/icons/ClassOutlined';
import SmsOutlined from '@material-ui/icons/SmsOutlined';

export default class NavBar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      botonIndex: 0
    };
  }

  componentDidMount() {
  }

  onButtonPress(index) {
    this.props.onButtonPress(index)
    if (this.state.botonIndex != index)
      this.setState({ botonIndex: index })
  }

  render() {
    return (
      <div
        style={{ position: 'absolute', top: 0, left: 0, right: 0, height: !this.props.isMobile && !this.props.focus ? '100%' : '10%', width: '100%' }}>
        <Grid
          container direction={!this.props.isMobile && !this.props.focus ? 'column' : 'row'} justify='space-evenly' alignItems='center' spacing={8}
          style={{ width: '100%', height: '100%' }}>
          <Grid item>
            {
              !this.props.isMobile && this.props.focus ?
                <AnimatedButton
                  buttonText={"EXPERIENCIA"}
                  onButtonPress={() => this.onButtonPress(0)}
                  color={'#a1a1a1'}
                  colorPressed={'#2b2b2b'}
                  lineColor={'#302b63'}
                  pressed={this.state.botonIndex == 0} />
                : <ButtonBase
                  centerRipple
                  onMouseEnter={() => null}
                  onMouseLeave={() => null}
                  onClick={() => this.onButtonPress(0)}
                >
                  <ClassOutlined style={{ color: this.state.botonIndex == 0 ? '#2b2b2b' : '#a1a1a1' }}/>
                  {this.state.botonIndex == 0 ?
                    <div style={{ position: 'absolute', bottom: '-10px', width: '5px', height: '5px', borderRadius: '50%', backgroundColor: '#302b63' }}></div>
                    : null
                  }
                </ButtonBase>

            }
          </Grid>
          <Grid item>
            {
              !this.props.isMobile && this.props.focus ?
                <AnimatedButton
                  color={'#a1a1a1'}
                  colorPressed={'#2b2b2b'}
                  lineColor={'#302b63'}
                  buttonText={"PROYECTOS"}
                  onButtonPress={() => this.onButtonPress(1)}
                  pressed={this.state.botonIndex == 1} />
                : <ButtonBase
                  centerRipple
                  onMouseEnter={() => null}
                  onMouseLeave={() => null}
                  onClick={() => this.onButtonPress(1)}
                  style={{ color: '#2b2b2b' }}
                >
                  <CodeOutlined style={{ color: this.state.botonIndex == 1 ? '#2b2b2b' : '#a1a1a1' }} />
                  {this.state.botonIndex == 1 ?
                    <div style={{ position: 'absolute', bottom: '-10px', width: '5px', height: '5px', borderRadius: '50%', backgroundColor: '#302b63' }}></div>
                    : null
                  }
                </ButtonBase>
            }
          </Grid>
          <Grid item>
            {
             !this.props.isMobile && this.props.focus ?
                <AnimatedButton
                  color={'#a1a1a1'}
                  colorPressed={'#2b2b2b'}
                  lineColor={'#302b63'}
                  buttonText={"CONTACTO"}
                  onButtonPress={() => this.onButtonPress(2)}
                  pressed={this.state.botonIndex == 2} />
                : <ButtonBase
                  centerRipple
                  onMouseEnter={() => null}
                  onMouseLeave={() => null}
                  onClick={() => this.onButtonPress(2)}
                  style={{ color: '#2b2b2b' }}
                >
                  <SmsOutlined style={{ color: this.state.botonIndex == 2 ? '#2b2b2b' : '#a1a1a1' }} />
                  {this.state.botonIndex == 2 ?
                    <div style={{ position: 'absolute', bottom: '-10px', width: '5px', height: '5px', borderRadius: '50%', backgroundColor: '#302b63' }}></div>
                    : null
                  }
                </ButtonBase>
            }
          </Grid>
        </Grid>
      </div>
    )
  }
}