import React, { Component } from 'react';
import { Grid, Typography, Hidden, ButtonBase, Button } from '@material-ui/core';
import styled, { keyframes } from 'styled-components'
import PropTypes from 'prop-types';

import SectionNav from './SectionNav'

import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';

import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';

import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';

import Stepper from './Stepper';
import CodeOutlined from '@material-ui/icons/CodeOutlined';
import LaptopMac from '@material-ui/icons/LaptopMac';
import Android from '@material-ui/icons/Android';
import KeyboardArrowDownOutlined from '@material-ui/icons/KeyboardArrowDownOutlined';

const ProyectosDiv = styled.div`
position:absolute;
bottom:0;
left:${props => props.isMobile ? 0 : props.sectionFocus >= 2 ? '10%' : '90%'};
width: ${props => props.isMobile ? '100%' : props.sectionFocus == 2 ? '85%' : '5%'};
height: ${props => !props.isMobile ? '100%' : props.sectionFocus == 2 ? '85%' : '4em'};
top: ${props => !props.isMobile ? 0 : props.sectionFocus < 2 ? '90%' : '4em'};
transition: all 2s;
overflow: hidden;
background: #468E8F;
background: ${props => props.sectionFocus == 2 ? '-webkit-linear-gradient(to bottom, #468E8F 1%, #fff 30%)' : props.isMobile ? '#468E8F' : '-webkit-linear-gradient(to bottom, #468E8F, #fff)'};
background: ${props => props.sectionFocus == 2 ? 'linear-gradient(to bottom, #468E8F 1%, #fff 30%)' : props.isMobile ? '#468E8F' : 'linear-gradient(to bottom, #468E8F, #fff)'};
`
const AnimatedCard = styled.div`
  display: inline-block;
  width: ${props => props.isMobile ? '86%' : '60%'};
  height:80%;
  position:absolute;
  top:10%;  
  left:${props => props.focusedElementIndex == props.elementIndex ? props.isMobile ? '7%' : '20%' : 0}; 
  border-radius:10px;
  background-color:transparent;
  z-index:  ${props =>
    props.hoveredIndex == props.elementIndex ? 
    11
    : props.isMobile || props.elementIndex == props.focusedElementIndex ?
     10 
     : props.elementIndex < props.focusedElementIndex? 
     10+props.elementIntex 
     : 10-props.elementIndex };
  transform: ${props =>
    !props.isMobile ?
      props.elementIndex < props.focusedElementIndex ?
        props.hoveredIndex == props.elementIndex ?
          'translate(-40%,0%) rotateZ(-5deg)'
          : 'translate(-70%,0%) rotateZ(0deg)'
        : props.elementIndex > props.focusedElementIndex ?
          props.hoveredIndex == props.elementIndex ?
            'translate(120%,-5%) rotateZ(5deg)'
            : 'translate(140%,0%) rotateZ(0deg)'
          : 'translate(0,-10%) rotateZ(0)'
      : props.focusedElementIndex == props.elementIndex ?
        'translate(0,-10%) rotateZ(0)'
        : props.elementIndex > props.focusedElementIndex ?
          'translate(-150%,0) rotateZ(0)'
          : 'translate(150%,0) rotateZ(0)'
  };
  transition: all 1s;
  -webkit-box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.5);
  -moz-box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.5);
  box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.5);
  animation: ${ props => props.focusedElementIndex == props.elementIndex ?
    keyframes`
    from {      
    }
    to {
      translate(0,0) rotateZ(0) scale(1);
    }`
    : null
  } 1s ease-in-out;
  animation-fill-mode: forwards;
`
const AnimCardImg = styled.div`{
  background-position: center;
  background-size: cover;
  background-image: url(${props => props.img});
  height: 100%;
  width:100%;
  transition: all 0.3s ease;
  opacity:${props => props.hoveredContent ? 0.5 : 1};
  transform: ${props => props.hoveredContent ? 'scale(1.3)' : null};
}`
const DevTextDescription = styled.div`
  width:90%;
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity:0;
  animation: ${ props => props.show ?
    keyframes`
    from {   
      opacity:0;   
    }
    to {
      opacity:1;
    }`
    : null
  } 1s ease-in-out;
  animation-fill-mode: forwards;
`
const IconHoveredAnimation = keyframes`
0% {
  opacity:1;
  transform: rotateX(0deg) rotateY(0deg);
}
15% {
  opacity:0.4;
}
25% {
  opacity:1;
  transform: rotateX(360deg) rotateY(360deg);
}
100% {
  opacity:1;
  transform: rotateX(360deg) rotateY(360deg);
}`
const BottomIcon = styled.div`
  margin-left:15px;
  margin-right:15px;  
  display:flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  &:hover{
    animation: ${IconHoveredAnimation} 2s ease-in-out;
  }
`
const Hashtags = styled.div`
  width:100%;
  display:flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  opacity:0;
  animation: ${ props => props.show ?
    keyframes`
    from {   
      opacity:0;   
    }
    to {
      opacity:1;
    }`
    : null
  } 1s ease-in-out;
  animation-fill-mode: forwards;
`
const Hashtag = styled.div`
  padding:2px 5px;
  background-color:rgba(0,0,0,0.5);
  margin: 0 5px;
  border-radius: 5%;
`

export default class Proyectos extends Component {
  constructor(props) {
    super(props);

    this.cardWidth = this.props.screenWidth

    this.state = {
      focusedElement: 1,
      hoveredContent: null
    };
  }

  handleFocus(index) {
    if (index != this.state.focusedElement) {
      this.setState({ focusedElement: index })
    }
  }

  onHoverContent(index) {
    this.setState({ hoveredContent: index + 1 })
  }
  onHoverOutContent() {
    this.setState({ hoveredContent: null })
  }

  render() {
    const { isMobile, sectionFocus, labels } = this.props
    const { hoveredContent, focusedElement } = this.state
    const list = labels.proyectos.list
    return (
      <ProyectosDiv
      onClick={() => this.props.onClick()}
        sectionFocus={sectionFocus} isMobile={isMobile}>
        <SectionNav labels={labels} section={2} isMobile={isMobile} focus={sectionFocus == 2} onSelect={() => this.props.onClick()} />

        <div style={{ visibility: sectionFocus == 2 ? 'visible' : 'hidden', width: '100%', height: '90%', padding: 0, overflow: 'hidden', position: 'absolute', top: '10%' }}>
          {
            list.map((proy, index) => (
              <AnimatedCard
                key={index}
                isMobile={isMobile}
                focusedElementIndex={focusedElement}
                elementIndex={index + 1}
                hoveredIndex={hoveredContent}
                cardWidth={this.cardWidth}
                innerMargin={this.innerMargin}
                onMouseEnter={() => this.onHoverContent(index)}
                onMouseLeave={() => this.onHoverOutContent()}
                onClick={() => this.handleFocus(index + 1)}
                >
                {
                  !isMobile ?
                    <div style={{
                      position: 'absolute', width: '100%', height: '100%', overflow: 'hidden', borderRadius: '10px', backgroundColor: '#2b2b2b'
                    }}>
                      <AnimCardImg img={proy.img} hoveredContent={hoveredContent == index + 1} />
                    </div>
                    : null
                }
                <div style={{
                  height: this.props.isMobile || index + 1 == this.state.hoveredContent && index + 1 == this.state.focusedElement ? '100%' : '50%',
                  width: '100%',
                  position: 'absolute',
                  bottom: 0,
                  left: 0,
                  borderRadius: 10,
                  transition: 'all 0.5s',
                  backgroundColor: 'rgba(255,255,255,0.8)',
                  background: '#000000',
                  background: '-webkit-linear-gradient(to top,rgba(0,0,0,1), rgba(7, 43, 42, 0.5), transparent)',
                  background: 'linear-gradient(to top, rgba(0,0,0,1), rgba(7, 43, 42, 0.5), transparent)',
                }} />
                <div style={{
                  height: '100%',
                  width: '100%',
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'space-around',
                  alignItems: 'center',
                }} >
                  <Hashtags show={isMobile || index + 1 == hoveredContent && index + 1 == focusedElement}>
                    {
                      (proy.hashtags.slice(0, isMobile ? 3 : proy.hashtags.length)).map((tag, index) => (
                        <Hashtag key={"" + index + tag}><Typography variant="subtitle1" style={{ fontSize: '0.9em', color: '#fff', fontWeight: 300, whiteSpace: 'normal', textAlign: 'center' }}>{tag}</Typography></Hashtag>
                      ))
                    }
                  </Hashtags>
                  <DevTextDescription show={isMobile || index + 1 == hoveredContent && index + 1 == focusedElement}>
                    <div style={{ overflowY: 'auto' }}>
                      <Typography variant="subtitle1" style={{ fontSize: '1em', color: '#fff', fontWeight: 400, whiteSpace: 'normal', textAlign: 'justify' }}>{proy.descripcion}</Typography>
                    </div>
                  </DevTextDescription>
                  <div style={{
                    width: '100%',
                    display: 'flex',
                    flexDirection: 'row',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                  }} >
                    <Typography variant="h1" style={{ fontSize: '1.5em', color: '#fff', fontWeight: 300 , marginLeft:20}}>{proy.titulo.toUpperCase()}</Typography>
                    <div style={{
                      display: 'flex',
                      flexDirection: 'row',
                      justifyContent: 'flex-end',
                      alignItems: 'center',
                      marginRight:20
                    }} >
                      {
                        index + 1 == focusedElement && proy.linkCodigo &&
                          <BottomIcon left>
                            <a href={proy.linkCodigo} target="_blank" rel="noopener noreferrer" ><CodeOutlined style={{ color: '#fff', fontSize: 28 }} /></a>
                          </BottomIcon>
                      }
                      {
                        index + 1 == focusedElement && proy.linkPagina &&
                          <BottomIcon>
                            <a href={proy.linkPagina} target="_blank" rel="noopener noreferrer" ><LaptopMac style={{ color: '#fff', fontSize: 28 }} /></a>
                          </BottomIcon>
                      }
                      {
                        index + 1 == focusedElement && proy.linkPlayStore &&
                          <BottomIcon>
                            <a href={proy.linkPlayStore} target="_blank" rel="noopener noreferrer" ><Android style={{ color: '#fff', fontSize: 28 }} /></a>
                          </BottomIcon>
                      }
                    </div>
                  </div>
                </div>
              </AnimatedCard>
            ))
          }

        </div>

        <div style={{ visibility: this.props.sectionFocus == 2 ? 'visible' : 'hidden', width: '100%', height: '10%', position: 'absolute', bottom: 0 }}>
          <Stepper listLength={list.length} onSelect={(index) => this.handleFocus(index + 1)} selected={focusedElement - 1} />
        </div>
      </ProyectosDiv>
    )
  }
}

Proyectos.propTypes = {
  isMobile: PropTypes.bool.isRequired
};
Proyectos.defaultProps = {
};
