import React, { Component } from 'react';
import { Button, Typography } from '@material-ui/core';
import styled, { keyframes } from 'styled-components'

import EmailOutlined from '@material-ui/icons/EmailOutlined';
import SectionNav from './SectionNav'

const ContactoDiv = styled.div`
position:absolute;
bottom:0;
left:${props => props.isMobile ? 0 : props.sectionFocus >= 3 ?  '15%' : '95%'};
width: ${props => props.isMobile ? '100%' : props.sectionFocus == 3 ? '85%' : '5%'};
top: ${props => !props.isMobile ? 0 : props.sectionFocus < 3  ? '95%' : '15%'};
transition: all 2s;
height: 100%;
overflow: hidden;
background: #B2D7B5;
background: ${props => props.sectionFocus == 1 ? '-webkit-linear-gradient(to bottom, #B2D7B5 1%, #fff 30%)' : '-webkit-linear-gradient(to bottom, #B2D7B5, #fff)'};
background: ${props => props.sectionFocus == 1 ? 'linear-gradient(to bottom, #B2D7B5 1%, #fff 30%)' : 'linear-gradient(to bottom, #B2D7B5, #fff)'};
`
const ContactCard = styled.div`
  width:85%;
  height:75px;
  background:transparent;
  margin:15px;
  border-radius:10px;
  border-width:3px;
  border-color:transparent;
  border-style:solid;
  border-color:rgba(7, 43, 42, 0.8);
  -webkit-box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.5);
  -moz-box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.5);
  box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.5);
  display:flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  opacity:0;
  animation: ${
    keyframes`
      from{
        opacity:0;
        transform: translate(0,20%);
      }
      to{
        opacity:1;
        transform: translate(0,0);
      }
    `
  } 1s ease-in-out;
  animation-fill-mode: forwards;
  animation-delay:${props=>props.delay}s;
`
const ContactCardHover = styled(ContactCard) `
  width:50%;
  border-color:rgba(7, 43, 42, 0.2);
  -webkit-box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.2);
  -moz-box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.2);
  box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.2);
  &:hover{
    border-color:rgba(7, 43, 42, 0.8);
    -webkit-box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.5);
    -moz-box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.5);
    box-shadow: 18px 17px 32px -4px rgba(43,43,43,0.5);
    width:55%;
    height:85px;
  }
  transition: all 0.5s ease-in-out;
  cursor:pointer;
`

const LinkedinIcon =  <img src={require('../assets/linkedin-icon.png')} style={{width:'35px', height:'35px'}}/> 
const GithubIcon =  <img src={require('../assets/github-icon.svg')} style={{width:'35px', height:'35px'}}/> 

export default class Contacto extends Component {
  constructor(props) {
    super(props);

    this.state = {
    };
  }

  onSendMail(){
    window.location.href = "mailto:mail@example.org";
  }
  onGithub(){
    window.open("https://github.com/leandroercoli", "_blank")
  }
  onLinkedin(){
    window.open("https://www.linkedin.com/in/leandroercoli", "_blank")
  } 

  render() {
    const {labels} = this.props
    return (
      <ContactoDiv
      onClick={() => this.props.onClick()}
        sectionFocus={this.props.sectionFocus} isMobile={this.props.isMobile}>
         <SectionNav labels={labels} section={3} isMobile={this.props.isMobile} focus={this.props.sectionFocus==3}  onSelect={() => this.props.onClick()}/>
      
      <div
        style={{ visibility: this.props.sectionFocus == 3 ? 'visible' : 'hidden', position: 'absolute', width: '100%', height: '100%', margin: 0, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
        {
          this.props.isMobile ?
            [
                 <ContactCard key={"cardMail"} onClick={()=>this.onSendMail()} delay={0}>
                <EmailOutlined fontSize='large' style={{ color: '#2b2b2b', margin: '0 10px' }} />
                <Typography variant="h1" style={{ fontSize: '1.2em', color: '#2b2b2b', fontWeight: 300, margin: '0 10px' }}>leandroercoli@gmail.com</Typography>
              </ContactCard>,
              <ContactCard key={"cardGithub"} onClick={()=>this.onGithub()} delay={0.5}>
                {GithubIcon}
                <Typography variant="h1" style={{ fontSize: '1.2em', color: '#2b2b2b', fontWeight: 300, margin: '0 10px' }}>/leandroercoli</Typography>
              </ContactCard>,
              <ContactCard key={"cardLinkedin"} onClick={()=>this.onLinkedin()} delay={1}>
                {LinkedinIcon}
                <Typography variant="h1" style={{ fontSize: '1.2em', color: '#2b2b2b', fontWeight: 300, margin: '0 10px' }}>/leandroercoli</Typography>
              </ContactCard>
            ]
            : [
              <ContactCardHover key={"cardMail"} onClick={()=>this.onSendMail()} delay={0}>
                <EmailOutlined fontSize='large' style={{ color: '#2b2b2b', margin: '0 10px' }} />
                <Typography variant="h1" style={{ fontSize: '1.5em', color: '#2b2b2b', fontWeight: 300, margin: '0 10px' }}>leandroercoli@gmail.com</Typography>
              </ContactCardHover>,
              <ContactCardHover key={"cardGithub"} onClick={()=>this.onGithub()} delay={0.5}>
                {GithubIcon}
                <Typography variant="h1" style={{ fontSize: '1.5em', color: '#2b2b2b', fontWeight: 300, margin: '0 10px' }}>/leandroercoli</Typography>
              </ContactCardHover>,
              <ContactCardHover key={"cardLinkedin"} onClick={()=>this.onLinkedin()} delay={1}>
                {LinkedinIcon}
                <Typography variant="h1" style={{ fontSize: '1.5em', color: '#2b2b2b', fontWeight: 300, margin: '0 10px' }}>/leandroercoli</Typography>
              </ContactCardHover>
            ]
        }
      </div>

      </ContactoDiv>
    );
  }
}
