import React, { Component } from 'react';
import { Grid, Typography, ButtonBase, Button } from '@material-ui/core';
import styled, { keyframes } from 'styled-components'
import LandingLeft from './LandingLeft'
import NavBar from './NavBar'
import Proyectos from './Proyectos'
import Experiencia from './Experiencia'
import Contacto from './Contacto'
import AnimatedButton from './AnimatedButton'

import CodeOutlined from '@material-ui/icons/CodeOutlined';
import ClassOutlined from '@material-ui/icons/ClassOutlined';
import SmsOutlined from '@material-ui/icons/SmsOutlined';
import {labelsEng,labelsEsp} from './Const'

const LandingRight = styled.div`
  position:absolute;
  bottom:0;
  right:0;
  width: ${props => props.isMobile ? '100%' : props.focusOnRight ? '85%' : '15%'};
  top: ${props => !props.isMobile ? 0 : !props.focusOnRight ? '85%' : '10%'};
  transition: all 2s;
  height: 100%;
  overflow: hidden;
  background: #fff;
 `

export default class LandingPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      section: 0,
      focusOnRight: false,
      hoverOnImg: false,
      labels:labelsEsp
    };
  }

  onMouseEnterSection(sectionNumber) {
    this.setState({ section: sectionNumber })
  }

  onButtonPress(index) {
    this.setState({ section: index })
  }

  onImgHover() {
    this.setState({ hoverOnImg: true })
  }

  onImgHoverOut() {
    this.setState({ hoverOnImg: false })
  }

  onEspaniol = () =>{
    this.setState({labels:labelsEsp})
  }
  onEnglish = () =>{
    this.setState({labels:labelsEng})
  }

  render() {
    const {labels} = this.state
    return (
      <div style={{
        width: '100%', height: '100%', overflow: 'hidden', 
        backgroundColor: this.state.section == 0 ? '#072b2a' : this.state.section == 1 ? '#1C4854' : this.state.section == 2 ? '#468E8F' : '#B2D7B5'
      }}>
        <LandingLeft
          onMouseEnter={() => this.onMouseEnterSection(0)}
          isMobile={this.props.isMobile}
          labels={labels}
          focus={this.state.section == 0}
          screenWidth={this.props.screenWidth}
          screenHeight={this.props.screenHeight}
          onEspaniol={this.onEspaniol}
          onEnglish={this.onEnglish}
           />

        <Experiencia
          onClick={() => this.onMouseEnterSection(1)}
          isMobile={this.props.isMobile}
          labels={labels}
          sectionFocus={this.state.section}
          screenWidth={this.props.isMobile ? this.props.screenWidth : this.state.section == 1 ? this.props.screenWidth * 0.85 : this.props.screenWidth * 0.3}
          screenHeight={this.props.isMobile ? this.state.section == 1 ? this.props.screenHeight * 0.8 : 0 : this.props.screenHeight * 0.9} />

        <Proyectos
          onClick={() => this.onMouseEnterSection(2)}
          isMobile={this.props.isMobile}
          labels={labels}
          sectionFocus={this.state.section}
          screenWidth={this.props.isMobile ? this.props.screenWidth : this.state.section == 2  ? this.props.screenWidth * 0.85 : this.props.screenWidth * 0.3}
          screenHeight={this.props.isMobile ? this.state.section == 2  ? this.props.screenHeight * 0.8 : 0 : this.props.screenHeight * 0.9} />

        <Contacto
          onClick={() => this.onMouseEnterSection(3)}
          isMobile={this.props.isMobile}
          labels={labels}
          sectionFocus={this.state.section}
          screenWidth={this.props.isMobile ? this.props.screenWidth : this.state.section == 3 ? this.props.screenWidth * 0.85 : this.props.screenWidth * 0.3}
          screenHeight={this.props.isMobile ? this.state.section == 3 ? this.props.screenHeight * 0.8 : 0 : this.props.screenHeight * 0.9} />  

      </div >
    )

  }
}

